.nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    background: #fff;
    height: .4rem;
    line-height: .4rem;
    border-bottom: 1px solid rgba(7,17,27,.1);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.nav>.nav-item {
    font-size: .14rem;
    color: #666;
    text-decoration: none;
    font-weight: 0;
}
.nav>.active{
    color: red;
}


